.App {
  text-align: center;
  min-height: 100vh;
  background-color: #282c34;
  color: white;
}

.App-header {
  padding: 20px;
  background-color: #1a1d23;
}

.App-header h1 {
  margin: 0;
  font-size: 2rem;
}

main {
  padding: 20px;
  max-width: 800px;
  margin: 0 auto;
}

.sip-status {
  margin-top: 20px;
  padding: 20px;
  background-color: #1a1d23;
  border-radius: 8px;
}

.status-indicator {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  margin: 15px 0;
}

.status-dot {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background-color: #666;
}

.status-dot.connected {
  background-color: #4CAF50;
  box-shadow: 0 0 8px #4CAF50;
}

.status-dot.disconnected {
  background-color: #f44336;
  box-shadow: 0 0 8px #f44336;
}

.status-dot.error {
  background-color: #ff9800;
  box-shadow: 0 0 8px #ff9800;
}

.error {
  color: #f44336;
  margin: 10px 0;
  padding: 10px;
  background-color: rgba(244, 67, 54, 0.1);
  border-radius: 4px;
}

.device-status {
  margin-top: 10px;
  font-size: 0.9rem;
  color: #aaa;
}

.call-info {
  margin: 20px 0;
  padding: 20px;
  background-color: #1a1d23;
  border-radius: 8px;
  animation: fadeIn 0.3s ease-in;
}

.call-info h3 {
  margin: 0 0 10px 0;
  color: #4CAF50;
}

.call-info p {
  margin: 0;
  font-size: 1.2rem;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.call-controls {
  margin-top: 20px;
  padding: 20px;
  background-color: #1a1d23;
  border-radius: 8px;
}

.button-group {
  display: flex;
  gap: 10px;
  justify-content: center;
  margin-top: 15px;
}

button {
  padding: 10px 20px;
  border: none;
  border-radius: 4px;
  font-size: 1rem;
  cursor: pointer;
  transition: background-color 0.2s;
}

.answer-button {
  background-color: #4CAF50;
  color: white;
}

.answer-button:hover {
  background-color: #45a049;
}

.reject-button {
  background-color: #f44336;
  color: white;
}

.reject-button:hover {
  background-color: #da190b;
}

.hangup-button {
  background-color: #ff9800;
  color: white;
}

.hangup-button:hover {
  background-color: #e68a00;
} 